function ArraySetting(){
    for (var i = 0; i < 4; i++) {
        imgSrcArray.push(document.getElementById('screen' + i));
        radioArray.push(document.getElementsByName('radio' + i));
        cardArray.push(document.getElementById('card' + i));
    }
}

function ArrayCompare(){
    //console.log(prevChangeArray == currChangeArray);
    for (var i = 0; i < radioArray.length; i++) {
        if (prevChangeArray[i] != currChangeArray[i]) {
            //console.log('yeah', i);
            cardArray[i].style.transition = "0.2s";
            cardArray[i].style.opacity = "0";
            // cardArray[i].style.border = "4px solid #9FD800";
        }else{
            // cardArray[i].style.border = "1px solid rgba(0, 0, 0, 0.1)";
            cardArray[i].style.opacity = "1";
        }
        prevChangeArray[i] = currChangeArray[i];
    }

}


function checkRadio(screenMode){
    var screenState;
    var fileType;
    if (screenMode == 0){
        screenState = 'desktop';
        fileType = 'jpg';
    }else{
        screenState = 'mobile';
        fileType = 'png';
    }
    for (var i = 0; i < radioArray.length; i++) {
        for (var j = 0; j < imgSrcArray.length; j++) {
            if (radioArray[i][j].checked) {
                // do whatever you want with the checked radio
                //imgSrcArray[i].src = './assets/img/screen/' + screenState + '/' + i + '/' + j + '.' + fileType;
                imgSrcArray[i].src = './assets/img/screen/' + i + '/' + j + '.png';
                currChangeArray[i] = j;
                // only one radio can be logically checked, don't check the rest
                break;
            }
        }
    }
}


